@import url("./hero.css");
@import url("./about.css");
@import url("./experience.css");
@import url("./reset.css");


@media (max-width: 768px) {
  body {
      font-size: 14px;
  }
  .container {
      padding: 10px;
  }
}



/* –ù–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ ‚Äî —Å–∫—Ä—ã—Ç–æ */
.fade-in {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 1s ease-out, transform 1s ease-out;
  }
  
  /* –ö–æ–≥–¥–∞ —ç–ª–µ–º–µ–Ω—Ç –≤–∏–¥–µ–Ω ‚Äî –ø–ª–∞–≤–Ω–æ–µ –ø–æ—è–≤–ª–µ–Ω–∏–µ */
  .fade-in.visible {
    opacity: 1;
    transform: translateY(0);
  }

  /* –û–±—â–∏–π —Ü–≤–µ—Ç —Ñ–æ–Ω–∞ –¥–ª—è –≤—Å–µ–≥–æ —Å–∞–π—Ç–∞ */
body {
    background-color: #111; /* –¶–≤–µ—Ç –∑–∞–¥–Ω–µ–≥–æ —Ñ–æ–Ω–∞ */
    color: white; /* –¶–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é */
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif; /* –£—Å—Ç–∞–Ω–æ–≤–∏–º —à—Ä–∏—Ñ—Ç */
}


.space-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -1;
  overflow: hidden;
  background: radial-gradient(circle at center, rgba(10, 10, 10, 0.8), rgba(0, 0, 0, 1));
}



/* –ö–Ω–æ–ø–∫–∞ –≤–≤–µ—Ä—Ö */
.scroll-to-top {
  position: fixed;
  bottom: 200px;
  right: 200px;
  width: 100px;
  height: 100px;
  background: rgba(58, 58, 58, 0.9);
  color: black;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;
  opacity: 0;
  transform: scale(0.8) translateY(20px);
  z-index: 10000;
  pointer-events: none; /* –ö–Ω–æ–ø–∫–∞ –Ω–µ –∫–ª–∏–∫–∞–±–µ–ª—å–Ω–∞, –∫–æ–≥–¥–∞ —Å–∫—Ä—ã—Ç–∞ */
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è */
.scroll-to-top.show {
  opacity: 1;
  transform: scale(1) translateY(0);
  pointer-events: auto; /* –î–µ–ª–∞–µ–º –∫–Ω–æ–ø–∫—É –∫–ª–∏–∫–∞–±–µ–ª—å–Ω–æ–π */
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –∏—Å—á–µ–∑–Ω–æ–≤–µ–Ω–∏—è */
.scroll-to-top.hide {
  opacity: 0;
  transform: scale(0.8) translateY(20px); /* –ö–Ω–æ–ø–∫–∞ —É–º–µ–Ω—å—à–∞–µ—Ç—Å—è –∏ —É—Ö–æ–¥–∏—Ç –≤–Ω–∏–∑ */
  pointer-events: none; /* –ù–µ –¥–∞–µ–º –∫–ª–∏–∫–∞—Ç—å –≤–æ –≤—Ä–µ–º—è –∏—Å—á–µ–∑–Ω–æ–≤–µ–Ω–∏—è */
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
.scroll-to-top:hover {
  background: #e6b800;
  transform: scale(1.1) translateY(-3px);
  box-shadow: 0px 8px 15px rgba(255, 204, 0, 0.5);
  transition: transform 0.2s ease-out, box-shadow 0.2s ease-out;
}

/* –≠—Ñ—Ñ–µ–∫—Ç –∫–ª–∏–∫–∞ */
.scroll-to-top:active {
  transform: scale(0.9) translateY(1px);
  box-shadow: 0px 4px 10px rgba(255, 204, 0, 0.7);
}

@media (max-width: 768px) {
  .scroll-to-top {
      bottom: 20px;
      right: 20px;
      width: 50px;
      height: 50px;
  }
}



html, body {
  overflow-y: auto !important;
}















  
