/* –û–±—â–∏–µ —Å—Ç–∏–ª–∏ —Å–µ–∫—Ü–∏–∏ */
.about-section {
    background: #11111100;
    color: white;
    text-align: center;
    padding: 120px 50px;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
.about-content {
    max-width: 1000px;
    margin: 0 auto;
    font-size: 21px;
    line-height: 1.6;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
.about-content h2 {
    font-size: 28px;
    color: #ffffff;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px; /* –†–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –ª–∏–Ω–∏—è–º–∏ –∏ —Ç–µ–∫—Å—Ç–æ–º */
    width: 100%;
    position: relative;
    margin-bottom: 80px;
}

/* –¢–µ–∫—Å—Ç —Å –≤—ã–¥–µ–ª–µ–Ω–∏–µ–º */
.about-content p {
    margin-bottom: 20px;
}

/* –í—ã–¥–µ–ª–µ–Ω–Ω—ã–µ —Å–ª–æ–≤–∞ */
.about-content strong {
    color: #ffcc00d1;
    font-weight: bold;
}


/* –õ–∏–Ω–∏–∏ —Å–ª–µ–≤–∞ –∏ —Å–ø—Ä–∞–≤–∞ */
.about-content h2::before,
.about-content h2::after {
    content: "";
    flex-grow: 0;
    height: 2px;
    background-color: #ffffff;
}

/* –î–ª–∏–Ω–∞ –ª–∏–Ω–∏–π */
.about-content h2::before {
    width: 100px; /* –î–ª–∏–Ω–∞ –ª–µ–≤–æ–π –ª–∏–Ω–∏–∏ */
}

.about-content h2::after {
    width: 100px; /* –î–ª–∏–Ω–∞ –ø—Ä–∞–≤–æ–π –ª–∏–Ω–∏–∏ */
}


@media (max-width: 768px) {
    .about-content {
        max-width: 90%;
        font-size: 16px;
    }
}
